		resetFilterPrice() {
			this.currentFilterPrice = -1
		},
		confirm() {
			this.isShowFilters = false
		},
		setCurrentFilterType(index) {
			this.currentFilterType = index
		},
		setCurrentFilterPrice(index) {
			this.currentFilterPrice = index
		}
	}
}
